















































































































































































































































import server from '../../../store/server.js';
//import prompt from '../../../components/prompt.vue';
export default {
	/*components: {
		'prompt': prompt
	},*/
	onLoad: function(option) {
		console.log(option.id);
		this.Token = server.net.GetSessionToken();
		this.id = option.id;
		this.getDetails(option.id);
		this.pageId = option.id;


	},
	onShow:function(){
		if(this.isfirstopen!=0){
		   console.log("页面显示");
		   this.AutographList = [];
		   this.isfirstopen = 0;
		   this.getDetails(this.id);
		}
	},
	data() {
		return {
			Token: '',
			id:'',
			ProjectName: '',
			Position: '',
			Engineering: '',
			Angle: '',
			Grade: '',
			StartDate: '',
			EndDate: '',
			dayTg: '',
			nightTg: '',
			ZNigNum: '',
			ZDayNum: '',
			PDayNum: '',
			PNigNum: '',
			MechanicalZNum: '',
			MechanicalMNum: '',
			MechanicalPNum: '',
			LabourName: '',
			LabourPhone: '',
			RebarName: '',
			RebarPhone: '',
			CarpentryName: '',
			CarpentryPhone: '',
			PlumberName: '',
			PlumberPhone: '',
			AutographList: [],
			pageId: '',
			TypeName: '',
			NeedAutograph: false,
			sendVal: '',
			isfirstopen:0
		}
	},
	methods: {
		openMask(index) {
			this.sendVal = true;
		},
		confirm() {
			console.log('点击了取消');
		},
		getDetails(id) { //获取详情数据
			let url = server.net.setUrl('api/ConcretePouringApi/Detail?id=' + JSON.parse(id));
			console.log(url);
			uni.request({
				url: url,
				method: 'POST',
				header: {
					//Token:server.net.getSessionToken(),
					Token: this.Token
				},
				success: res => {
					server.net.showWrongTipsEx(res);
					let result = res.data;
					let jsondata = result.data;
					console.log(res)
					if (result.status == 1) {
						console.log("数据获取成功");
						this.ProjectName = jsondata.Name;
						this.Position = jsondata.Position;
						this.Engineering = jsondata.Engineering;
						this.Angle = jsondata.Angle;
						this.Grade = jsondata.Grade;
						this.StartDate = jsondata.StartDate.substring(0, 10);
						this.EndDate = jsondata.EndDate.substring(0, 10);
						this.dayTg = jsondata.TDayNum;
						this.nightTg = jsondata.TNigNum;
						this.ZDayNum = jsondata.ZDayNum;
						this.ZNigNum = jsondata.ZNigNum;
						this.PDayNum = jsondata.PDayNum;
						this.PNigNum = jsondata.PNigNum;
						this.MechanicalZNum = jsondata.MechanicalZNum;
						this.MechanicalMNum = jsondata.MechanicalMNum;
						this.MechanicalPNum = jsondata.MechanicalPNum;
						this.LabourName = jsondata.LabourName;
						this.LabourPhone = jsondata.LabourPhone;
						this.RebarName = jsondata.RebarName;
						this.RebarPhone = jsondata.RebarPhone;
						this.CarpentryName = jsondata.CarpentryName;
						this.CarpentryPhone = jsondata.CarpentryPhone;
						this.PlumberName = jsondata.PlumberName;
						this.PlumberPhone = jsondata.PlumberPhone;
						jsondata.AutographList.forEach((e) => {
							this.AutographList.push({
								Type:e.Type,
								TypeName: e.TypeName,
								AccountID: e.AccountID,
								AccountName: e.AccountName,
								IsFinish: e.IsFinish,
								NeedAutograph: e.NeedAutograph
							});
						})
					} else {
						console.log(result.message)
					}
				},
				fail: () => {
					console.log("数据获取失败");
				},
				complete: () => {}
			});
		},
		autograph(id,type){
		    this.isfirstopen = 1;
		    uni.navigateTo({
		    	 url: '../../../components/Autograph?id=' + id + '&type=' + type
		    })
		}
	}

}



